<template>
  <v-app>
    <v-app-bar app color="blue-grey" dark>
      <h1 class="font-weight-black mx-auto">MINEFIELD</h1>
    </v-app-bar>

    <v-main>
      <h2 class="font-weight-bold text-center error--text">{{ loseText }}</h2>
      <h2 class="font-weight-bold text-center success--text">
        {{ winText }}
      </h2>
      <div
        v-if="!isPlay"
        style="height: 90vh"
        class="d-flex flex-column justify-space-around align-center"
      >
        <v-btn x-large width="200px" @click="set(8)">Easy</v-btn>
        <v-btn x-large width="200px" @click="set(12)">Medium</v-btn>
        <v-btn x-large width="200px" @click="set(16)">Hard</v-btn>
      </div>

      <div v-else class="d-sm-flex justify-center">
        <div class="d-flex flex-column mr-10">
          <v-btn x-large class="mt-10" @click="set(n)">New game</v-btn>
          <v-btn x-large class="mt-10" @click="isPlay = false">Back</v-btn>
        </div>
        <canvas
          :width="step * n"
          :height="step * n"
          id="board"
          @click="click"
          @contextmenu.prevent="leftClick"
          @mousemove="highlight"
          class="mt-10"
        >
        </canvas>
        <v-card class="ml-10 mt-10" height="50px">
          <v-card-title>Remaining Bombs: {{ remainingBombs }}</v-card-title>
        </v-card>
      </div>
      <img id="bomb" style="display: none" src="./assets/bomb.png" />
      <img id="flag" style="display: none" src="./assets/flag.png" />
    </v-main>
  </v-app>
</template>

<script>
export default {
  name: "App",

  data: function() {
    return {
      n: 0,
      isPlay: false,
      step: 40,
      color: [
        "blue",
        "green",
        "red",
        "purple",
        "yellow",
        "black",
        "orange",
        "brown"
      ],
      bombs: [],
      loseText: "",
      winText: "",
      remainingBombs: 0,
      isGameOver: false, //for remove event listeners
    };
  },
  computed: {
    field: function() {
      var field = new Array(this.n);
      for (let i = 0; i < this.n; i++) {
        field[i] = new Array(this.n);
        field[i].fill(0);
      }
      return field;
    },
    numBomb: function() {
      return Math.floor((this.n * this.n) / 6);
    },
    flagField: function() {
      var field = new Array(this.n);
      for (let i = 0; i < this.n; i++) {
        field[i] = new Array(this.n);
        field[i].fill(0);
      }
      return field;
    }
  },
  methods: {
    set: function(num) {
      this.n = num;
      this.isPlay = true;
      for (let i = 0; i < this.n; i++) {
        this.field[i].fill(0);
        this.flagField[i].fill(0);
      }
      this.bombs = [];
      this.loseText = "";
      this.winText = "";
      this.remainingBombs = this.numBomb;
      this.isGameOver = false;
      setTimeout(this.initCanvas, 100);
      setTimeout(this.generateBombs, 100);
    },
    findPos: function(x, y) {
      return [Math.floor(x / this.step), Math.floor(y / this.step)];
    },
    adj: function(x, y) {
      return [
        [x - 1, y - 1],
        [x - 1, y],
        [x - 1, y + 1],
        [x, y - 1],
        [x, y + 1],
        [x + 1, y - 1],
        [x + 1, y],
        [x + 1, y + 1]
      ];
    },
    click: function(e) {
      if(this.isGameOver) return;
      var canvas = document.getElementById("board");
      var x = e.pageX - canvas.offsetLeft;
      var y = e.pageY - canvas.offsetTop - (3 / 2) * this.step;
      var pos = this.findPos(x, y);
      this.drawPos(pos);
    },
    drawPos: function(pos) {
      //already marked
      if (this.field[pos[0]][pos[1]] == -1) return;
      //lose cond
      if (this.field[pos[0]][pos[1]] == 1) {
        this.gameOver(pos[0], pos[1]);
        return;
      }
      //if flag
      if (this.flagField[pos[0]][pos[1]] == 1) {
        this.remainingBombs++;
        this.flagField[pos[0]][pos[1]] == 0;
      }
      //draw
      var num = this.calcBomb(pos[0], pos[1]);
      this.vueCanvas.fillStyle = "#e5e8e8";
      this.vueCanvas.fillRect(
        pos[0] * this.step,
        pos[1] * this.step,
        this.step,
        this.step
      );
      this.vueCanvas.textAlign = "center";
      this.vueCanvas.font = "bold 30px Arial";
      this.vueCanvas.fillStyle = this.color[num - 1];
      this.vueCanvas.fillText(
        num.toString(),
        pos[0] * this.step + this.step / 2,
        pos[1] * this.step + (this.step / 4) * 3
      );
      this.field[pos[0]][pos[1]] = -1;
      this.drawGrid(this.vueCanvas);
      if (num == 0) {
        var adj = this.adj(pos[0], pos[1]);
        for (let i = 0; i < adj.length; i++) {
          if (
            adj[i][0] < 0 ||
            adj[i][0] >= this.n ||
            adj[i][1] < 0 ||
            adj[i][1] >= this.n
          )
            continue;
          this.drawPos(adj[i]);
        }
      }
      if (this.isWon()) return;
    },
    leftClick: function(e) {
      if(this.isGameOver) return;
      var canvas = document.getElementById("board");
      var x = e.pageX - canvas.offsetLeft;
      var y = e.pageY - canvas.offsetTop - (3 / 2) * this.step;
      var pos = this.findPos(x, y);
      if (
        this.field[pos[0]][pos[1]] != -1 &&
        this.flagField[pos[0]][pos[1]] == 0
      ) {
        var img = document.getElementById("flag");
        this.vueCanvas.drawImage(
          img,
          pos[0] * this.step,
          pos[1] * this.step,
          this.step,
          this.step
        );
        this.remainingBombs--;
        this.flagField[pos[0]][pos[1]] = 1;
      } else if (this.flagField[pos[0]][pos[1]] == 1) {
        this.flagField[pos[0]][pos[1]] = 0;
        this.vueCanvas.fillStyle = "#bdc3c7";
        this.vueCanvas.fillRect(
          pos[0] * this.step,
          pos[1] * this.step,
          this.step,
          this.step
        );
        this.remainingBombs++;
      }
      this.drawGrid(this.vueCanvas);
    },
    highlight: function(e) {
      if(this.isGameOver) return;
      var canvas = document.getElementById("board");
      var x = e.pageX - canvas.offsetLeft;
      var y = e.pageY - canvas.offsetTop - (3 / 2) * this.step;
      var pos = this.findPos(x, y);
      if (
        this.field[pos[0]][pos[1]] == -1 ||
        this.flagField[pos[0]][pos[1]] == 1
      )
        return;
      this.vueCanvas.fillStyle = "rgba(240, 243, 244, 0.5)";
      this.vueCanvas.fillRect(
        pos[0] * this.step,
        pos[1] * this.step,
        this.step,
        this.step
      );
      this.stopHighlight(pos);
      this.drawGrid(this.vueCanvas);
    },
    stopHighlight: function(pos) {
      for(let i = 0; i < this.n; i++) {
        for(let j = 0; j < this.n; j++) {
          if (
            this.field[i][j] != -1 &&
            this.flagField[i][j] == 0 &&
            (i != pos[0] || j != pos[1])
          ) {
            this.vueCanvas.fillStyle = "#bdc3c7";
            this.vueCanvas.fillRect(i * this.step, j * this.step, this.step, this.step);
          }
        }
      }
    },
    isWon: function() {
      for (let i = 0; i < this.n; i++) {
        for (let j = 0; j < this.n; j++) {
          if (this.field[i][j] == 0) return false;
        }
      }
      this.winText = "GAME WON!";
      this.isGameOver = true;
      return true;
    },
    gameOver: function(x, y) {
      this.vueCanvas.globalAlpha = 0.5;
      this.vueCanvas.fillStyle = "red";
      this.vueCanvas.fillRect(
        x * this.step,
        y * this.step,
        this.step,
        this.step
      );
      var that = this;
      var img = document.getElementById("bomb");
      this.bombs.forEach(e => {
        that.vueCanvas.drawImage(
          img,
          e[0] * that.step,
          e[1] * that.step,
          that.step,
          that.step
        );
      });
      this.vueCanvas.globalAlpha = 1;
      this.drawGrid(this.vueCanvas);
      this.loseText = "GAME OVER!";
      //remove event
      this.isGameOver = true;
    },
    initCanvas: function() {
      var canvas = document.getElementById("board");
      var ctx = canvas.getContext("2d");
      this.vueCanvas = ctx;
      ctx.fillStyle = " #bdc3c7 ";
      ctx.fillRect(0, 0, this.step * this.n, this.step * this.n);
      this.drawGrid(ctx);
    },
    drawGrid: function(c) {
      var width = this.step * this.n;
      c.strokeStyle = 'white';
      for (let x = 0; x <= width; x += this.step) {
        c.moveTo(x, 0);
        c.lineTo(x, width);
        c.stroke();
      }
      for (let y = 0; y <= width; y += this.step) {
        c.moveTo(0, y);
        c.lineTo(width, y);
        c.stroke();
      }
    },
    generateBombs: function() {
      for (let i = 0; i < this.numBomb; i++) {
        var bombX, bombY;
        do {
          (bombX = Math.floor(Math.random() * this.n)),
            (bombY = Math.floor(Math.random() * this.n));
        } while (this.field[bombX][bombY] == 1);
        this.field[bombX][bombY] = 1;
        this.bombs.push([bombX, bombY]);
      }
    },
    calcBomb: function(x, y) {
      var adj = this.adj(x, y);
      var res = 0;
      for (let i = 0; i < adj.length; i++) {
        if (
          adj[i][0] < 0 ||
          adj[i][0] >= this.n ||
          adj[i][1] < 0 ||
          adj[i][1] >= this.n
        )
          continue;
        if (this.field[adj[i][0]][adj[i][1]] == 1) res++;
      }
      return res;
    }
  }
};
</script>
